  <style scoped>
  .blog-detail-layout {
    display: flex;
    justify-content: space-between;
    margin: 20px;
  }
  
  .left-card,
  .right-card {
    flex: 1;
    margin: 10px;
    padding: 20px;
    background: #f5f5f5;
    border-radius: 8px;
  }
  
  .main-card {
    flex: 3;
    margin: 10px;
    padding: 20px;
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .author-info h3,
  .other-blogs h3 {
    margin-bottom: 10px;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  ul li a {
    text-decoration: none;
    color: #333;
    transition: color 0.3s;
  }
  
  ul li a:hover {
    color: #007bff;
  }
  
  .blog-time {
    font-size: 0.9rem;
    color: #888;
    margin-bottom: 10px;
  }
  
  .blog-description {
    font-size: 1rem;
    color: #666;
    margin-bottom: 20px;
  }
  
  .blog-content {
    font-size: 1.1rem;
    color: #444;
  }
  .mhy-avatar {
     display: inline-block;
     position: relative;
   }
  .mhy-avatar__img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 1px solid #ebebeb;
    vertical-align: top;
  }
   .mhy-avatar__img {
     width: 100%;
     height: 100%;
     border-radius: 50%;
     border: 1px solid #ebebeb;
     vertical-align: top;
   }
   img {
     border-style: none;
   }
   .mhy-account-center-header__avatar {
    margin-right: 24px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
  }
   .mhy-avatar__xxl {
     width: 120px;
     height: 120px;
   }
  </style>
   